@import "node_modules/bootstrap/scss/functions";
//@import "node_modules/bootstrap/scss/variables";

//$btn-box-shadow:              false;
//$btn-focus-box-shadow:        false;
//$btn-active-box-shadow:       false;

$enable-shadows: false;
$enable-gradients: false;
//$enable-important-utilities: false;
$enable-negative-margins: true;
$enable-rfs: false;

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1140px,
        xxl: 1216px //1320
);

$border-radius:               1rem;
$border-radius-sm:            1rem;
$border-radius-lg:            1rem;

$border-width: 2px;

$blue:    #13A5EF;
$red:     #E44545;
$green:   #4BAE3B;

$light: #D2E7F2;
$secondary: #313E5B;
//$gray-600: #6c757d;
$gray-600: #C2CDD6;
$body-color:  $secondary;
$border-color: rgba(49, 62, 91, 0.08);

$btn-close-opacity:          1;
$btn-close-hover-opacity:    .75;
//$btn-close-focus-opacity:    1;

// font size
$font-size-base:              1rem;
$font-size-sm:                .875rem; //$font-size-base;
$font-size-lg:                $font-size-base * 1.125;
$h1-font-size:                $font-size-base * 2.25;

$small-font-size: .875rem;

/*$display-font-sizes: (
        1: 5rem,
        2: 4.5rem,
        3: 4rem,
        4: 3.5rem,
        5: 3rem,
        6: 2.5rem
) !default;*/


//$input-btn-font-size:         1rem;

// font weight
$font-weight-bold:            600;
$headings-font-weight:        $font-weight-bold;
$btn-font-weight:             $font-weight-bold;
$display-font-weight:         $font-weight-bold;


$btn-padding-y:               13px;
$btn-padding-x:               34px;
$btn-border-width:            2px;

$input-border-width: 1px;
$input-border-color: #D3DDE2;
$input-border-radius: 4px;

$nav-link-font-weight:              600;
$nav-link-color:                    $secondary;
$nav-link-hover-color:              $blue;

// <a>
$link-color:                              $nav-link-color;
$link-decoration:    none;
$link-hover-color:                        $nav-link-hover-color;
$link-hover-decoration:                   underline;



$spacer: 2rem;
$spacers: (
        0: 0,
        1: $spacer * .25,
        2: $spacer * .5,
        3: $spacer,
        4: $spacer * 1.5,
        5: $spacer * 3,
) !default;

$spacers: map-merge($spacers, (
        6: $spacer * 2,
        7: $spacer * 4,
        8: $spacer * .75
));


$line-height-base: 1.6;


$nav-tabs-border-width:            0;
$nav-tabs-border-radius:           none;

$container-padding-x: 1.5rem;


//$grid-gutter-width: 1rem;

// modal
$modal-lg: 720px;
$modal-md: 576px;
$modal-dialog-margin: 1.5rem;
$modal-dialog-margin-y-sm-up: 2rem;
$modal-title-line-height: 140%;
$modal-backdrop-bg:                 $secondary;
$modal-backdrop-opacity:            .8;
//$modal-content-box-shadow-xs:       $box-shadow-sm !default;
//$modal-content-box-shadow-sm-up:    $box-shadow !default;

//$box-shadow:                  0px 30px 100px rgba(16, 53, 135, 0.15);
//$box-shadow-sm:               none;
$input-btn-focus-box-shadow: none;

$input-btn-padding-x:         .5rem;
$input-padding-x:                       1rem;
$input-padding-y: .75rem;

$input-height-border:                   $input-border-width * 2;
//$form-floating-height:            add(2.75rem, $input-height-border);
$form-floating-height:            add(2.5rem, $input-height-border);
$form-floating-line-height:       1.6;
$form-floating-padding-x:         1.5rem;
$form-floating-padding-y:         .5rem;
$form-floating-input-padding-t:   .75rem;
$form-floating-input-padding-b:   .75rem;
$form-floating-label-opacity:     1;
$form-floating-label-transform:   scale(0.75) translateY(-1.25rem) translateX(0.15rem);
$form-floating-transition:        opacity .1s ease-in-out, transform .1s ease-in-out;

$accordion-icon-color: $blue;
$accordion-button-icon:         url("data:image/svg+xml,<svg viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M38 22H26V10a2 2 0 10-4 0v12H10a2 2 0 100 4h12v12a2 2 0 004 0V26h12a2 2 0 000-4z' fill='#{$accordion-icon-color}'/></svg>");
$accordion-button-active-icon:  url("data:image/svg+xml,<svg viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M38 22H10a2 2 0 100 4h28a2 2 0 000-4z' fill='#{$accordion-icon-color}'/></svg>");
$accordion-icon-width: 1.5rem;
$accordion-button-padding-y:              .75rem;
$accordion-padding-x:                     0;
$accordion-button-active-bg:              transparent;
$accordion-button-active-color: inherit;

$input-btn-focus-box-shadow: none;

$hr-opacity: 1;
$table-border-color: $light;
$table-group-separator-color: $light;

$table-caption-color: inherit;

$form-feedback-icon-valid:          url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg'><path d='M14.72 8.79l-4.29 4.3-1.65-1.65a1 1 0 10-1.41 1.41l2.35 2.36a1 1 0 001.41 0l5-5a1.002 1.002 0 10-1.41-1.42zM12 2a10 10 0 100 20 10 10 0 000-20zm0 18a8 8 0 110-16.001A8 8 0 0112 20z' fill='#{$green}'/></svg>");
$form-feedback-icon-invalid:        url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg'><path d='M12 22c5.523 0 10-4.477 10-10S17.523 2 12 2 2 6.477 2 12s4.477 10 10 10zm3-13l-6 6m0-6l6 6' stroke='#{$red}' stroke-width='2' stroke-linecap='round' stroke-linejoin='round' fill='none' /></svg>");


@import "node_modules/bootstrap/scss/bootstrap";
@import 'node_modules/@tkrotoff/bootstrap-floating-label/src/bootstrap-floating-label';

.table>:not(:first-child) {
  border-top-width: $table-border-width;
}
