.btn {
  &:focus {
    box-shadow: none;
  }
}

.btn-primary {
  color: #fff;

  &:hover {
    background: #4DC5F8;
    color: #fff;
    border-color: #4DC5F8;
  }

  &:active,
  &:focus {
    background: #0C8BD3;
    color: #fff;
    border-color: #0C8BD3;
  }
}

.btn-outline-secondary {
  color: #313E5B;

  &:hover {
    background: transparent;
    color: #13A5EF;
    border-color: #13A5EF;
  }

  &:active,
  &:focus {
    background: transparent;
    color: #0C8BD3;
    border-color: #0C8BD3;
  }
}

.btn-outline-primary {
  &:hover {
    background: transparent;
    color: #4DC5F8;
    border-color: #4DC5F8;
  }

  &:active,
  &:focus {
    background: transparent;
    color: #0C8BD3;
    border-color: #0C8BD3;
  }
}

.offcanvas {
  max-width: 80%;
}

.modal {

  &-title {

  }

  &-dialog {
    margin-top: 2rem;
    margin-bottom: 2rem;

    &-centered {
      min-height: calc(100% - 4rem);
    }
  }


  &-content {
    border: none !important;

    @include media-breakpoint-up('sm') {
      box-shadow: 0px 30px 100px rgba(16, 53, 135, 0.15);
    }
  }


}

.form-control {
  font-size: 14px;

  @include media-breakpoint-up('lg') {
    font-size: 18px;
  }
}

.form-floating {

  >label {
    color: rgba(49, 62, 91, 0.48);
    font-size: .875rem;
  }

  > .form-control:focus,
  > .form-control:not(:placeholder-shown),
  > .form-select {
    ~ label {
      left: 1rem;
      padding-left: .25rem;
      padding-right: .25rem;
      background: #fff;
    }
  }
}